	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>せっかちな方のための本日の戦績: 2,963円→3,145円(+6.1%)、以上</strong> 何事も経験っ,,╹◡╹,, というわけで、今年からFX始めてみることにしました！(実は年末からやってたりゴニョゴニョ)  とりま自己ルールを作ってみた。</p>\n<ul>\n<li>3,000円スタート、実弾の増資はトータル50万円まで。</li>\n<li>あんまりのめり込みすぎないよう、増資は月イチの小出しで。</li>\n<li>増資は原則勝ち進んでいる時だけ。負けても潔くロスカット。</li>\n<li>通貨ペアは気分とノリ次第で。(ポン円、次いでドル円が多め)</li>\n<li>ハイレバ。最低でも25倍。できれば3桁。</li>\n</ul>\n<hr>\n<p><strong>12/30</strong> 3,000円スタート。ペアは初めてということもあり、基本のドル円(USD)。9ヶ月くらい前に実弾15万円くらいで、個別株の現物デイトレやってたので、そのノリで始めてみる。 結果: 3,000円→2,963円 (-37円: -1.2%/累計-1.2%) めっちゃビビリなので、小出しで色々試してみたが、残念なことに一度もプラスになれず、あれよあれよと惨敗。</p>\n<hr>\n<p><strong>1/18</strong> リベンジ！リベンジ！リベンジ！ 今回の目標は、とりあえず元本を取り戻す！ ということで今回のペアは、ジェットコースターの異名を持つポン円(GBP)。あと前回ビビリ過ぎていたので、今回はもう少しガッツリ張ってみる。 結果: 2,963円→3,145円(+182円: +6.1%/累積+4.8%) <em>やったぜ。</em></p>","frontmatter":{"cover":null,"date":"2016-01-18T23:59:56+09:00","strDate":"2016/1/18","title":"FX始めました"}}},"pathContext":{"path":"/ja/blog/2016/01/18-fx始めました/","langKey":"ja"}}

